import asyncGet from 'get-async';
import { createCodeLanguages } from '../../utils/constants/languages';

const languages = createCodeLanguages();

export async function loadLanguageMode(key: string) {
  const mode = languages.find((l) => l.key === key)?.mode;
  if (!mode) { return key; }

  await asyncGet(`we-editor-code-language-${key}`, () => {
    return import(/* webpackChunkName: "codemirror-lang-[request]" */`codemirror/mode/${mode}/${mode}.js`);
  });

  return key;
}
